// Typography
$font-family: 'Sukhumvit Set', 'sand-serif';
$font-page-name: 'Bebas Neue', 'sand-serif';
$font-tequila-title: 'Kohinoor Bangla', 'sand-serif';
$font-tequila: 'Helvetica', 'sand-serif';

$font-tequilas: 'Lucida Grande',
'sans-serif';

$font-tequilas-heading: 'Kohinoor Bangla',
'sans-serif';

//colors
$body-bg: #fff;
$color-font: #606060;

:root{
  --body-bg: #fff;
  --color-font: #606060;
}

$bp: l;
// layout

:root{
  --header-height: 15vh;
  --maxHeight: 0;
  --padding: 8vw;
  --color-title: #fff;
  & .mobile{
    --padding: 1em;
    --header-height: 3.5rem;
  }
}

// z-index

:root{
  --z-back: -10;
  --z-normal: 1;
  --z-active: 50;
  --z-tooltip: 90;
  --z-banner: 75;
  --z-banner-c: 90;
  --z-layer: 200;
  --z-fixed: 500;
  --z-modal: 5000;
  --section-normal : 100;
  --section-active : 300;
  --z-up : 15000;
  --z-header: 1000;

  --side-bar-width: 32vw;
  --main-section-width: 68vw
}


