:root {
  --body-bg: #fff;
  --color-font: #606060;
}

:root {
  --header-height: 15vh;
  --maxHeight: 0;
  --padding: 8vw;
  --color-title: #fff;
}

:root .mobile {
  --padding: 1em;
  --header-height: 3.5rem;
}

:root {
  --z-back: -10;
  --z-normal: 1;
  --z-active: 50;
  --z-tooltip: 90;
  --z-banner: 75;
  --z-banner-c: 90;
  --z-layer: 200;
  --z-fixed: 500;
  --z-modal: 5000;
  --section-normal: 100;
  --section-active: 300;
  --z-up: 15000;
  --z-header: 1000;
  --side-bar-width: 32vw;
  --main-section-width: 68vw
;
}

.loading {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: var(--z-modal);
}

.loading img {
  width: 200px;
}

@media screen and (max-width: 800px) {
  .loading img {
    width: 100px;
  }
}

.portfolio__list-title::before {
  content: "+";
  margin-right: 0.3em;
}

.portfolio__list-title.show::before {
  content: "-";
}
