@import "config/variables";

.loading {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: $body-bg;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: var(--z-modal);
  //display: none;
  img {
    width: 200px;
    @media screen and(max-width: 800px) {
      width: 100px;
    }
  }
}

.portfolio__list-title::before {
  content: "+";
  margin-right: 0.3em;
}

.portfolio__list-title.show::before {
  content: "-";
}
